.deleteButton{
    overflow: hidden;
    cursor: pointer;
    position: absolute;
    display: none;
}

.editSpan{

    background-image: url('/src/pictures/editButton.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    filter: brightness(0) invert(1);
    height: 18px;
    width: 18px;
    display: inline-block;
    cursor: pointer;
    transition: transform .2s;
  }

  .deleteSpan{

    background-image: url('/src/pictures/removeButton.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    filter: brightness(0) invert(1);
    height: 18px;
    width: 18px;
    display: inline-block;
    cursor: pointer;
    transition: transform .2s;
  }
  